.search-form {
  display: flex;

  justify-content: center;
  margin-bottom: 50px;
}

input {
  display: block;
  margin: 0;
  font: inherit;
  padding: 8px;
  max-width: 320px;
  border: 1px solid grey;
  border-right: none;
}

button {
  display: block;
  margin: 0;
  border: 1px solid grey;
  cursor: pointer;
}
.load-more {
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}



.gallery {
  display: grid;
  max-width: 1140px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 380px;
  grid-gap: 12px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.photo-card {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.info {
  padding: 15px;
}
img {
  display: block;
  height: 250px;
  max-width: 100%;
  object-fit: cover;
}
p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}